import { html } from 'lit';
import { OmniElement } from '../../omni-element.js';
import { foundationsBadge, professionalBadge, endToEndBadge } from '../../assets/badges.js';
import { property } from 'lit/decorators.js';

import {css} from 'lit';
const styles = css`:host{height:2em;width:2em}`


type Level = 'omni foundations' | 'omni professional' | 'omni end-to-end';

/**
 * User certification badges
 *
 * To include within your project `import { OmniBadgeElement } from 'omni-ui';`
 *
 * ```html
 * <omni-style>
 *   <omni-badge class="is-size-1" level="omni foundations">
 *   </omni-badge>
 * </omni-style>
 * ```
 * @tagname omni-badge
 */
export class OmniBadgeElement extends OmniElement {
  /** arial label attribute to specify badge */
  @property({ type: String, attribute: 'aria-label', reflect: true }) ariaLabel = 'Certification badge';
  /** specify level of badge */
  @property({ type: String, reflect: true }) level: Level = 'omni foundations';

  static get styles() {
    return [...super.styles, styles];
  }

  renderBadge() {
    switch (this.level.toLowerCase()) {
      case 'omni foundations':
        return foundationsBadge(html);
      case 'omni professional':
        return professionalBadge(html);
      case 'omni end-to-end':
        return endToEndBadge(html);
      default:
        console.error(`The level attribute '${this.level}' does not match an omni-badge case`);
        return '';
    }
  }

  render() {
    return html`<div part="badge">${this.renderBadge()}</div>`;
  }
}

OmniElement.register('omni-badge', OmniBadgeElement);

declare global {
  interface HTMLElementTagNameMap {
    'omni-badge': OmniBadgeElement;
  }
}
